body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: #fff;
  background: -webkit-gradient(linear, right top, left top, from(#64b3f4), to(#c2e59c));
  background: -webkit-linear-gradient(right, #64b3f4, #c2e59c);
  background: -o-linear-gradient(right, #64b3f4, #c2e59c);
  background: linear-gradient(270deg, #64b3f4, #c2e59c);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
  animation: Gradient 15s ease infinite;
}

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}



